export class TaskRunner {
  constructor () {
    this.count = null
    this.current = null
    this.callbacks = null
  }

  //  connt :5
  Run (count, callbacks) {
    this.count = count
    this.current = 0
    this.callbacks = callbacks
    if (count === 0) {
      alert('run 0')
      // this.TaskReady ();
    } else {
      this.RunOnce()
    }
  }

  // TODO 
  // 
  RunOnce () {
    console.log(this.callbacks, 'this.callbacks in RunOnce ')
    // this.callbacks.runTask(this.current, this.TaskReady.bind(this))
    setTimeout(() => {
      this.callbacks.runTask(this.current, this.TaskReady.bind(this))
    }, 0)
  }

  TaskReady () {
    // console.log(this, 'gaga')
    this.current += 1
    if (this.current < this.count) {
      this.RunOnce()
    } else {
      // 
      if (this.callbacks.onReady) {
        this.callbacks.onReady()
      }
    }
  }
}
export function RunTasks (count, callbacks) {
  let taskRunner = new TaskRunner()
  taskRunner.Run(count, callbacks)
}
