
const state = {
  info: '',
  pointsDistance: 0,
  parallelFacesDistance: 0,
  facesAngle: 0,
  isActive:false,
  showMeasureIInfo:false

}
// 
const mutations = {
  ShowMeasureInfo(state,val){
     state.showMeasureIInfo = val
  },
  IsActive(state,val){
    state.isActive = val
  },
  // 
  AddInfo (state, val) {
    state.info = val
  },
  // 
  AddPointsDistance (state, val) {
    state.pointsDistance = val
  },
  // 
  AddDistanceParallel (state, val) {
    state.parallelFacesDistance = val
  },
  // 
  AddAngel (state, val) {
    state.facesAngle = val
  },
  // 
  Reset () {
 
    state.pointsDistance = 0
    state.parallelFacesDistance = 0
    state.facesAngle = 0
    state.showMeasureIInfo = false
  }

}
