export class ShowEdge {
  constructor (website) {
    // // test start  mainObject undefined
    // // console.log(viewer, 'viewer in ShowEdge')
    // this.viewer = viewer
    // this.mainObject = viewer.geometry.mainObject
    // // console.log(this.mainObject, ' this.mainObject in ShowEdge')
    // // test end
    console.log(website,'websitein showedge')
    this.viewer = website.viewer
    this.mainObject = website.model
    console.log(this.mainObject, ' this.mainObject in ShowEdge')
    this.mainEdgeObject = null
    this.edgeSettings = {
      showEdges: false,
      edgeColor: new RGBColor(0, 0, 0),
      edgeThreshold: 1
    }
  }

  UpdateWorldMatrix () {
    
    if (this.mainObject !== null) {
   
      this.mainObject.updateWorldMatrix(true, true)
    }
  }

  // TODO  
  EnumerateMeshes (enumerator) {
    console.log(this.mainObject, 'this.mainObject in modeltool')
    if (this.mainObject === null) {
      return
    }
    this.mainObject.traverse((obj) => {
      if (obj.isMesh) {
        // console.log(obj, 'obj in EnumerateMeshes ')
        enumerator(obj)
      }
    })
  }

  EnumerateEdges (enumerator) {
    if (this.mainEdgeObject === null) {
      return
    }
    this.mainEdgeObject.traverse((obj) => {
      if (obj.isLineSegments) {
        enumerator(obj)
      }
    })
  }

  // edges
  SetEdgeSettings (model,show, color, threshold) {
    this.mainObject = model
    console.log(show, color, threshold, 'show, color, threshold')
    let needToGenerate = false
    //      
    if (
      show &&
      (!this.edgeSettings.showEdges ||
        this.edgeSettings.edgeThreshold !== threshold)
    ) {
      // alert('')
      needToGenerate = true
    }
    this.edgeSettings.showEdges = show
    this.edgeSettings.edgeThreshold = threshold
    this.edgeSettings.edgeColor = color
    console.log(this.mainObject, 'this.mainObject222222')
    console.log(this.edgeSettings, 'this.edgeSettings')
    if (this.mainObject === null) {
      return
    }
    if (this.edgeSettings.showEdges) {
      if (needToGenerate) {
        // alert(1)
        this.ClearMainEdgeObject()
        this.GenerateMainEdgeObject()
      } else {
        // alert(2)
        this.ClearMainEdgeObject()
        this.GenerateMainEdgeObject()
      }
    } else {
      this.ClearMainEdgeObject()
    }
  }

  GenerateMainEdgeObject () {
   
    console.log(this.mainObject,'this.mainObject1111')

    let edgeColor = ConvertColorToThreeColor(this.edgeSettings.edgeColor)
    console.log(edgeColor, 'edgeColor')
    this.mainEdgeObject = new THREE.Object3D()
    // TODO  
    this.UpdateWorldMatrix()

    this.EnumerateMeshes((mesh) => {
      // SetThreeMeshPolygonOffset(mesh, true)
      // console.log(mesh, 'mesh in EnumerateMeshes  modelTool')
      // ...
      let edges = new THREE.EdgesGeometry(
        mesh.geometry,
        this.edgeSettings.edgeThreshold

      )
      let line = new THREE.LineSegments(
        edges,
        new THREE.LineBasicMaterial({
          // color: edgeColor
          color: 'blue'

        })
      )
      line.applyMatrix4(mesh.matrixWorld)
      line.userData = mesh.userData
      line.visible = mesh.visible
      this.mainEdgeObject.add(line)
    })
    // this.scene.add(this.mainEdgeObject)
    console.log(this.mainEdgeObject, 'this.mainEdgeObject2')
    console.log(this.viewer,'this.viewer')
    this.viewer.scene.add(this.mainEdgeObject)
    console.log(this.mainEdgeObject, 'this.mainEdgeObject3')
  }

  ClearMainEdgeObject () {
    console.log(this.mainEdgeObject, 'this.mainEdgeObject1')
    if (this.mainEdgeObject === null) {
      return
    }

    this.EnumerateMeshes((mesh) => {
      // SetThreeMeshPolygonOffset(mesh, false);
    })
    this.EnumerateEdges((edge) => {
      edge.geometry.dispose()
    })
    this.viewer.scene.remove(this.mainEdgeObject)
    this.mainEdgeObject = null
  }
}

