import { FileSource, GetFileName, GetFileExtension, RequestUrl, RequestUrl2, ReadFile, testasync } from '../engine/io/fileutils.js'
import { RunTasks } from '../engine/core/taskrunner.js'

class InputFile {
  constructor (name, source, data) {
    this.name = name
    this.source = source
    this.data = data
  }
}
export class ImporterFile {
  constructor (name, source, data) {
    this.name = GetFileName(name)
    this.extension = GetFileExtension(name)
    this.source = source
    this.data = data
    this.content = null
    this.size = data.size
  }

  SetContent (content) {
    this.content = content
    console.log(this, content, 'this in setContent')
  }

  setSize (size) {
    this.size = size
  }
}
export class ImporterFileList {
  constructor () {
    this.files = []
  }

  GetFiles () {
    return this.files
  }

  FillFromInputFiles (inputFiles) {
    this.files = []
    for (let inputFile of inputFiles) {
      let file = new ImporterFile(inputFile.name, inputFile.source, inputFile.data)
      this.files.push(file)
    }
  }

  // 
  GetContent (callbacks) {
    console.log(this.files, 'this.files in GetContent')
    RunTasks(this.files.length, {
      runTask: (index, onTaskComplete) => {
        console.log(index, 'index in runTask')
        // 
        callbacks.onFileListProgress(index, this.files.length)
        this.GetFileContent(this.files[index], {
          onReady: onTaskComplete,
          // onProgress: callbacks.onFileLoadProgress,
          onProgress: (loaded, total) => {
            console.log(loaded, total, 'load onProgress')
            // 
            this.files[index].setSize(total)
            callbacks.onFileLoadProgress(loaded, total)
          }
        })
      },
      // content
      onReady: callbacks.onReady
    })
  }

  GetFileContent (file, callbacks) {
    console.log(file, ' file in GetFileContent')
    let loaderPromise = null
    if (file.source === FileSource.Url) {
      loaderPromise = RequestUrl(file.data, callbacks.onProgress)// callback onprogess
    } else if (file.source === FileSource.File) {
      loaderPromise = ReadFile(file.data, callbacks.onProgress)
    }
    loaderPromise.then((content) => {
      console.log(content, file, 'content in loaderPromise')
      file.SetContent(content)
      console.log(content, file, 'content in loaderPromise2')
    }).catch((err) => {

    }).finally(() => {
      callbacks.onReady()
    })
  }
}
// url
export function InputFilesFromUrls (urls) {
  let inputFiles = []
  for (let url of urls) {
    console.log(url, 'url in InputFilesFromUrls')
    let fileName = GetFileName(url)
    inputFiles.push(new InputFile(fileName, FileSource.Url, url))
  }
  return inputFiles
}
// 
export function InputFilesFromFileObjects (fileObjects) {
  let inputFiles = []
  for (let fileObject of fileObjects) {
    console.log(fileObject, 'fileObject')
    let fileName = GetFileName(fileObject.name)
    inputFiles.push(new InputFile(fileName, FileSource.File, fileObject, fileObject.size))
  }
  return inputFiles
}
const LoaderUtils = {
  createFilesMap: function (files) {
    // const map = {}

    // for (let i = 0; i < files.length; i++) {
    //   const file = files[i]
    //   map[file.name] = file
    // }

    // return map

    const map = new Map()
    for (let i = 0; i < files.length; i++) {
      const file = files[i]
      map.set(file.name, files[i])
    }
    return map
  },
  getFilesFromItemList: function (items, onDone) {
      	// TOFIX: setURLModifier() breaks when the file being loaded is not in root
    let itemsCount = 0
    let itemsTotal = 0
    const files = []// file 
    const filesMap = {}
    function onEntryHandled () {
      // // https://blog.csdn.net/dylanns/article/details/101437724
      itemsCount++ // a++++a
      console.log(itemsCount, 'itemsCount')
      if (itemsCount === itemsTotal) { //  
        onDone(files, filesMap)
      }
    }
    function handleEntry (entry) {
      // 
      if (entry.isDirectory) {
        const reader = entry.createReader()
        reader.readEntries(function (entries) {
          for (let i = 0; i < entries.length; i++) {
            handleEntry(entries[i])
          }
          onEntryHandled()
        })
      } else if (entry.isFile) {
        entry.file(function (file) {
          console.log(file, 'file in entry.file')
          files.push(file)
          // filesMap[entry.fullPath.slice(1)] = file //fullPath :"/beats_highpoly.fbx"
          filesMap[entry.name] = file // {name:file}

          onEntryHandled()
        })
        console.log(entry, 'entry ')
        console.log(files, 'files in handleEntry')
      }
      itemsTotal++ // +1
    }
    for (let i = 0; i < items.length; i++) {
      const item = items[i]
      console.log(item, 'item in getFilesFromItemList')
      // 
      if (item.kind === 'file') {
        console.log(item.webkitGetAsEntry(), 'item.webkitGetAsEntry()')
        handleEntry(item.webkitGetAsEntry())
      }
    }
  }
}
export { LoaderUtils }
