import { RGBColor } from '../engine/model/color'
export const Theme =
{
  Light: 1,
  Dark: 2
}
export class Settings {
  constructor () {
    // edge
    this.showEdges = true
    this.edgeColor = new RGBColor(25, 25, 112)
    this.edgeThreshold = 0
    // theme
    this.themeId = Theme.Light
    // 
    this.environmentMapName = 'fishermans_bastion'
    this.backgroundIsEnvMap = true // 

    // add
    // scene
    this.sceneColor = 0xbfd1e5// 0xbfd1e5

    // camera
    this.angle = 50// 50
    this.nearest = 1// 1
    this.farthest = 30000// 30000
  }
}
