import modelEngine from './modelEngine.vue'
import Vue from 'vue'


const components = [ modelEngine ]
const install = function (Vue) {   
  //   
  // components.forEach(item => {   Vue.component('ModelEngine', item)   }) 
  components.forEach(item => {   Vue.component(item.name, item)   }) 

}
if (typeof window !== 'undefined' && window.Vue) {   
  install(window.Vue) 
} 
//  install install  
export default { install }