function addLoadModeltoScene (object, callbacks) {
  callbacks.onModelFinished(object)
}

export function isGLTF1 (contents) {
  let resultContent

  if (typeof contents === 'string') {
    // contents is a JSON string
    resultContent = contents
  } else {
    const magic = THREE.LoaderUtils.decodeText(new Uint8Array(contents, 0, 4))

    if (magic === 'glTF') {
      // contents is a .glb file; extract the version
      const version = new DataView(contents).getUint32(4, true)

      return version < 2
    } else {
      // contents is a .gltf file
      resultContent = THREE.LoaderUtils.decodeText(new Uint8Array(contents))
    }
  }

  const json = JSON.parse(resultContent)

  return (json.asset != undefined && json.asset.version[0] < 2)
}
export function handleJSON (data, callbacks) {
  if (data.metadata === undefined) { // 2.0
    data.metadata = { type: 'Geometry' }
  }

  if (data.metadata.type === undefined) { // 3.0
    data.metadata.type = 'Geometry'
  }

  if (data.metadata.formatVersion !== undefined) {
    data.metadata.version = data.metadata.formatVersion
  }

  switch (data.metadata.type.toLowerCase()) {
    case 'buffergeometry':

    {
      const loader = new THREE.BufferGeometryLoader()
      const result = loader.parse(data)

      const mesh = new THREE.Mesh(result)
      addLoadModeltoScene(mesh, callbacks)
      // editor.execute(new AddObjectCommand(editor, mesh))

      break
    }

    case 'geometry':

      console.error('Loader: "Geometry" is no longer supported.')

      break

    case 'object':

    {
      const loader = new THREE.ObjectLoader()
      loader.setResourcePath(scope.texturePath)

      loader.parse(data, function (result) {
        if (result.isScene) {
          alert('scene')
          // TODO 
          addLoadModeltoScene(result, callbacks)
          // editor.execute(new SetSceneCommand(editor, result))
        } else {
          addLoadModeltoScene(result, callbacks)
          // editor.execute(new AddObjectCommand(editor, result))
        }
      })

      break
    }

    case 'app':

      editor.fromJSON(data)

      break
  }
}
