import * as THREE from 'three'

// 
export class ViewerGeometry {
  constructor (scene) {
    console.log(scene, 'scene in ViewerGeometry')
    this.scene = scene
    this.mainObject = null
  }

  GetMainObject () {
    return this.mainObject
  }

  // 
  SetMainObject (mainObject) {
    this.mainObject = mainObject
    console.log(this.mainObject, 'this.mainObject')
    this.scene.add(this.mainObject)
  }

  // 
  Clear () {
    this.ClearMainObject()
  }

  ClearMainObject () {
    console.log(this.mainObject, 'this.mainObjectin ClearMainObject')
    if (this.mainObject === null) {
      return
    }

    this.EnumerateMeshes((mesh) => {
      console.log(mesh, 'mesh in EnumerateMeshes ')
      mesh.geometry.dispose()
    })
    this.scene.remove(this.mainObject)
    this.mainObject = null
  }

  //  
  EnumerateMeshes (enumerator) {
    console.log(this.mainObject, 'this.mainObject11111')
    if (this.mainObject === null) {
      return
    }
    this.mainObject.traverse((obj) => {
      if (obj.isMesh) {
        console.log(obj, 'obj in EnumerateMeshes ')
        enumerator(obj)
      }
    })
  }

  //  TODO add
  //   mesh
  GetMeshIntersectionUnderMouse (mouseCoords, camera, width, height) {
    console.log(mouseCoords, camera, width, height, 'in GetMeshIntersectionUnderMouse')
    if (this.mainObject === null) {
      return null
    }

    if (mouseCoords.x < 0.0 || mouseCoords.x > width || mouseCoords.y < 0.0 || mouseCoords.y > height) {
      return null
    }

    let raycaster = new THREE.Raycaster()
    let mousePos = new THREE.Vector2()
    mousePos.x = (mouseCoords.x / width) * 2 - 1
    mousePos.y = -(mouseCoords.y / height) * 2 + 1
    raycaster.setFromCamera(mousePos, camera)
    let iSectObjects = raycaster.intersectObject(this.mainObject, true)
    console.log(iSectObjects, 'iSectObjects')
    // if(iSectObjects.length>0){
    //     return  iSectObjects[0].object
    // }
    for (let i = 0; i < iSectObjects.length; i++) {
      let iSectObject = iSectObjects[i]
      if (iSectObject.object.type === 'Mesh' && iSectObject.object.visible) {
        return iSectObject
      }
    }

    return null
  }

  // 
  GetBoundingSphere (needToProcess) {
    let boundingBox = this.GetBoundingBox(needToProcess)
    console.log(boundingBox, 'boundingBox11')
    if (boundingBox === null) {
      return null
    }

    let boundingSphere = new THREE.Sphere()
    boundingBox.getBoundingSphere(boundingSphere)
    // 
    return boundingSphere
  }

  // mesh    
  GetBoundingBox (needToProcess) {
    // console.log(needToProcess, 'needToProcess')
    // TODO mesh   
    // let hasMesh = false
    // let boundingBox = new THREE.Box3()
    // this.EnumerateMeshes((mesh) => {
    //   console.log(mesh, 'mesh-GetBoundingBox')
    //   if (needToProcess(mesh.userData)) {
    //     let box = new THREE.Box3().setFromObject(mesh).getSize(new THREE.Vector3())

    //     console.log(box, 'size111111')
    //     console.log(new THREE.Box3().setFromObject(mesh), 'new THREE.Box3().setFromObject(mesh)')
    //     boundingBox.union(new THREE.Box3().setFromObject(mesh))
    //     hasMesh = true
    //   }
    // })
    // if (!hasMesh) {
    //   return null
    // }
    if (this.mainObject === null) {
      return
    }
    let boundingBox = new THREE.Box3().setFromObject(this.mainObject)
    // this.box3Helper = new THREE.Box3Helper(boundingBox, 0xffff00)
    const center = new THREE.Vector3() // 0,0,0
    boundingBox.getCenter(center)
    console.log('', center)
    // 
    // this.mainObject.position.set(-center.x, -center.y, -center.z)
    // this.scene.add(this.box3Helper)

    // console.log(boundingBox.max, 'max')
    // console.log(boundingBox.min, 'min')
    // const mdlen = boundingBox.max.x - boundingBox.min.x // 
    // const mdhei = boundingBox.max.y - boundingBox.min.y // 
    // const mdwid = boundingBox.max.z - boundingBox.min.z// 
    // 
    console.log(boundingBox, 'boundingBox in GetBoundingBox')

    return boundingBox
  }
}

// 
export class ViewerExtraGeometry {
  constructor (scene) {
    this.scene = scene
    this.ExtraObject = null
  }

  // 
  AddObject (object) {
    if (this.ExtraObject === null) {
      this.ExtraObject = new THREE.Object3D()
      this.scene.add(this.ExtraObject)
    }
    this.ExtraObject.add(object)
  }

  // 
  Clear () {
    if (this.ExtraObject === null) {
      return
    }
    this.ExtraObject.traverse((obj) => {
      if (obj.isMesh || obj.isLineSegments) {
        obj.geometry.dispose()
      }
    })
    this.scene.remove(this.ExtraObject)
    this.ExtraObject = null
  }
}
