  //  dom 
  SetUIState (uiState) {
    if (this.uiState === uiState) {
      return
    }
    this.uiState = uiState
    if (this.uiState === WebsiteUIState.Intro) {

    } else if (this.uiState === WebsiteUIState.Model) {
      ShowDomElement(this.parameters.mainDiv, true)
    } else if (this.uiState === WebsiteUIState.Loading) {

    }
    this.Resize()
  }

  // 
  
  OnModelClicked (button, mouseCoordinates) {

    // 
    // this.viewer.outline.Click2(mouseCoordinates) //
    this.viewer.outline.Click(mouseCoordinates)
    // console.log(this.viewer.outline.getSelectedObject().name,'getSelectedObject')
    // console.log(this, 'this in OnModelClicked')
    console.log(button, mouseCoordinates, 'mouseCoordinates in OnModelClicked')
    if (button !== 1) {
      return
    }
    if (this.measureTool.IsActive()) {
      this.measureTool.Click(mouseCoordinates)
    }
  }

  OnModelMouseMoved (mouseCoordinates) {
    // console.log(mouseCoordinates, 'mouseCoordinates')
    if (this.measureTool.IsActive()) {
      this.measureTool.MouseMove(mouseCoordinates)
    }
  }
 
