  //  
  SwitchTheme (newThemeId, resetColors) {
    console.log(newThemeId, 'newThemeId in SwitchTheme')
    this.settings.themeId = newThemeId
    this.themeHandler.SwitchTheme(this.settings.themeId)
  }

  // edge
  UpdateEdgeDisplay (show, val = 40) {
   
    this.settings.showEdges = show
    if (show) {
     
      this.settings.edgeThreshold = val
    } else {
      this.settings.edgeThreshold = 0
    }
    this.showEdge.SetEdgeSettings(
      this.model,
      this.settings.showEdges,
      this.settings.edgeColor,
      this.settings.edgeThreshold
    )
  }

  // 
  onEnvironmentMapChange () {
    this.UpdateEnvironmentMap()
  }

  UpdateEnvironmentMap () {
    // //
    // let envMapPath = '/static/envmaps/' + this.settings.environmentMapName + '/'
    let envMapPath = 'static/envmaps/' + this.settings.environmentMapName + '/'
    let envMapTextures = [
      envMapPath + 'posx.jpg',
      envMapPath + 'negx.jpg',
      envMapPath + 'posy.jpg',
      envMapPath + 'negy.jpg',
      envMapPath + 'posz.jpg',
      envMapPath + 'negz.jpg'
    ]
    this.viewer.SetEnvironmentMapSettings(envMapTextures, this.settings.backgroundIsEnvMap)
  }
