import { CreateUrlParser } from '../engine/parameters/parameterlist.js'
// http://127.0.0.1:5173/layout#model=https://xiaokaixuan.oss-cn-beijing.aliyuncs.com/fbx/jiaju/damo.obj,https://xiaokaixuan.oss-cn-beijing.aliyuncs.com/fbx/jiaju/damo.mtl,https://xiaokaixuan.oss-cn-beijing.aliyuncs.com/fbx/jiaju/Mat3d66_1265862_13_9264.jpg,https://xiaokaixuan.oss-cn-beijing.aliyuncs.com/fbx/jiaju/Mat3d66_1265862_2_7987.jpg,https://xiaokaixuan.oss-cn-beijing.aliyuncs.com/fbx/jiaju/Mat3d66_1265862_6_8580.jpg
export class HashHandler {
  constructor () {
    this.eventListener = null
    this.skipNextEvent = false
  }

  //  
  SetEventListener (eventListener) {
    this.eventListener = eventListener
    window.onhashchange = this.OnChange.bind(this)
  }

  //  hash 
  OnChange () {
    alert('OnChange')
    if (this.skipNextEvent) {
      alert('skipNextEvent')
    }
  }

  // hash
  HasHash () {
    let hash = this.GetHash()
    console.log(hash, 'hash-----')
    return hash.length > 0
  }

  // url hash
  GetHash () {
    // console.log(window.location.hash.substring(1), ' window.location.hash.substring(1)');
    return window.location.hash.substring(1)
  }

  //  url 
  GetModelFilesFromHash () {
    // 
    let parser = CreateUrlParser(this.GetHash())
    return parser.GetModelUrls()
  }
}
