// 
Blast (model, val) {
  this.model = model
  this.blastValue = val
  explodeModel(this.model,  this.blastValue)
  store.dispatch('app/changBlastVal',this.blastValue)
}
// edge
Edge(model,val){
  this.model = model
  store.commit('app/CHANGE_EDGE_VAL',val)
  this.website.UpdateEdgeDisplay(true,val)
}
// start(){
