<template>
  <div class="viewer">
    <div id="model-wrapper" class="model-wrapper">
      <div id="main_viewer" class="main_viewer" v-loading="showLoading" element-loading-text=""
        element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
      </div>
      <!-- :style="{ left: (isActive ? -100% : 0) }" -->
      <div class="side_menu_wrap" :class="[isActive ? 'left0' : 'left200']">
        <div class="side-menu">
          <!-- <button v-if="hasEyeDrop" @click="nativePick"></button>
          <input v-else type="color" @input="nativePick" /> -->
          <p class="label_row">
            <span></span>
          </p>
          <div class="btns">
            <!-- <span class="active"></span>
            <span></span>
            <span></span> -->
            <span :class="[tabIndex == index ? 'active' : '']" v-for="(item, index) in btns" :key="index"
              @click="backgroundTabClick(index)">{{
                item
              }}</span>
          </div>
          <p class="label_row">
            <span></span>
          </p>
          <div class="envMaps">
            <el-image v-for="(item, index) in   envMaps" :key="index" class="img"
              @click="SetEnvironment(item.name, item.backgroundIsEnvMap)" style="width: 45px; height: 45px"
              :src="item.url"></el-image>
          </div>
          <p class="label_row">
            <!-- <span> {{ getBlastVal }}{{ blastVal }}</span> -->
            <span>Edges</span>
          </p>
          <div class="blast-slider">
            <!--  slideValue -->
            <el-slider v-show="showSlider" v-model="edgeVal" :min="0" :max="100" @change="edgeChange">
            </el-slider>
          </div>
          <p class="label_row">
            <!-- <span> {{ getBlastVal }}{{ blastVal }}</span> -->
            <span></span>
          </p>
          <div class="blast-slider">
            <!--  slideValue -->
            <el-slider v-show="showSlider" v-model="blastVal" :min="1" :max="100" @change="blastChange">
            </el-slider>
          </div>

          <el-row class="row">
            <span></span>
            <el-switch @change="changeWange" v-model="wanggeValue" active-color="#13ce66" inactive-color="#ff4949">
            </el-switch>
          </el-row>
          <el-row class="row">
            <span></span>
            <el-switch @change="autoRotation" v-model="rotationValue" active-color="#13ce66" inactive-color="#ff4949">
            </el-switch>
          </el-row>

          <!-- <el-button @click="dragMeshs" :class="[dragMeshBtn ? 'active' : '']">
          {{ dragMeshBtn ? 'mesh' : 'mesh' }}
        </el-button> -->
          <p class="label_row">
            <span></span>
          </p>

          <div class="items">
            <!-- <span class="icon iconfont icon-yidong"></span>
            <div class="name">
              
            </div> -->
            <span :class="[ifduxian ? 'active' : '']" @click="duxian">
              <i class="icon  iconfont" :class="[ifduxian ? 'icon-quanbu' : 'icon-yanjing']"></i>
              <b>{{ ifduxian? '': '' }}</b>
            </span>
            <!-- <span>
              <i class="icon iconfont icon-quanxianshi-copy"></i>
              <b></b>
            </span>
            <span>
              <i class="icon iconfont icon-malatang"></i>
              <b></b>
            </span> -->
            <span :class="[dragMeshBtn ? 'active' : '']" @click="dragMesh">
              <i class="icon  iconfont icon-yidong"></i>
              <b></b>
            </span>
            <!-- <span @click="resetDragMesh">
              <i class="icon iconfont icon-ic_fuwei"></i>
              <b></b>
            </span> -->
            <span @click="reset">
              <i class="icon iconfont icon-ic_fuwei"></i>
              <b></b>
            </span>
            <span @click="celiang" :class="[MeasureisActive ? 'active' : '']">
              <i class="icon iconfont icon-chizi"></i>
              <b></b>
            </span>
            <span @click="yincang">
              <i class="icon iconfont icon-yincang"></i>
              <b></b>
            </span>

            <!-- <span :class="{ 'active': toumingBtn }" @click="touming">
              <i class="icon iconfont icon-dibiaotouming"></i>
              <b>{{ toumingBtn? '': '' }}</b>
            </span> -->
            <span @click="touming2">
              <i class="icon iconfont icon-toumingdutiaojie"></i>
              <b></b>
            </span>
          </div>
        </div>
        <div class="switch" @click="switchleft">
          <i class="icon iconfont " :class="[isActive ? 'icon-zuojiantou-copy ' : 'icon-zuojiantou']"></i>
        </div>
      </div>
      <div class="full-sceen" @click="fullScreen">
        <i class="icon iconfont " :class="[fullScreenBtn ? 'icon-quanping' : 'icon-quxiaoquanping']"
          style="font-size: 26px;"></i>
      </div>
      <div class="measureInfo" v-if="MeasureisActive">
        <div v-if="!showMeasureIInfo">
          <span> {{ info }}</span>
        </div>
        <div v-else>
          <span>
            <i class="icon iconfont icon-guanxianchangdu"></i>
            <b>{{ pointsDistance }}</b>
          </span>
          <!-- <span >
            <i class="icon iconfont icon-toumingdutiaojie"></i>
            <b>{{ parallelFacesDistance }}</b>
          </span> -->
          <span>
            <i class="icon iconfont icon-jiaodu"></i>
            <b>{{ facesAngle }}</b>
          </span>
        </div>

      </div>



      <!-- model: {{ model }}
      
      height }}
      elementui  -->
    </div>
  </div>

</template>
<script>
import {
  mapState,
  mapGetters,
  mapMutations
} from 'vuex'
// import Vue from 'vue';
// import { ElSwitch} from 'element-ui';
// Vue.component(ElSwitch.name, ElSwitch);
import { StartWebsite, SetWebsiteEventHandler } from './website/index.js'
import {fullScreen as fullScreens} from './modelTool/fullScreen.js'
import {openFullscreen,exitFullscreen,isFullscreenEnabled,getFullscreenElement,isFullScreen} from './modelTool/screen.js'
import store from '@/store'
 export default {
  name:'ModelEngine',
  props:{
    width: {
      type: String,
      default: '500',
      // default: '100%',
    },
    height: {
      type: String,
      default: '500',
      // default: '100%',
    },
  },  
  data(){
     return {
      // hasEyeDrop: 'EyeDropper' in window,
      website:null,
      wanggeValue:false,//
      rotationValue:false,//
      showSlider:true,//
      slideValue:0,
      btns:['','',''],//
            // 
       envMaps: [
        { name: '', url: 'static/envmaps/noimage.jpg', backgroundIsEnvMap: false },
        { name: 'teide', url: 'static/envmaps/teide/posx.jpg', backgroundIsEnvMap: true },
        // { name: 'park', url: '/static/envmaps/park/posx.jpg', backgroundIsEnvMap: true },
        // { name: 'maskonaive', url: '/static/envmaps/maskonaive/posx.jpg', backgroundIsEnvMap: true },
        // { name: 'fishermans_bastion', url: '/static/envmaps/fishermans_bastion/posx.jpg', backgroundIsEnvMap: true },
        // { name: 'citadella', url: '/static/envmaps/citadella/posx.jpg', backgroundIsEnvMap: true },
        // { name: 'ice_river', url: '/static/envmaps/ice_river/posx.jpg', backgroundIsEnvMap: true }
      ],
      tabIndex:0,
      model:null,
      dragMeshBtn:false,
      duxianBtn:false,
      toumingBtn:false,
      fullScreenBtn:true,
      celiangBtn:false,
      isActive:false

     }
  },
  computed:{
    ...mapState({
      showLoading:state=>state.app.showLoading,
      ifduxian:state=>state.app.duxian,
      // blast
      // blastVal:state=>state.blastVal
      // edge
      // edgeVal:state=>state.app.edgeValue,
      // measure 
      info:state=>state.measure.info,
      pointsDistance:state=>state.measure.pointsDistance,
      parallelFacesDistance:state=>state.measure.parallelFacesDistance,
      facesAngle:state=>state.measure.facesAngle,
      MeasureisActive:state=>state.measure.isActive,
      showMeasureIInfo:state=>state.measure.showMeasureIInfo
    }),
    // get set 
    blastVal:{
         get(){
             return this.$store.state.app.blastVal
         },
         set(val){
           this.$store.state.app.blastVal = val
            //  this.$store.commit('CHANGE_BLASTVAL',val)
         }
    },
    edgeVal:{
       get(){
             return this.$store.state.app.edgeVal
         },
         set(val){
             // this.$store.state.app.edgeVal = val
              this.$store.commit('app/CHANGE_EDGE_VAL',val)
           
         }
    },  
    

    // ,pointsDistance,parallelFacesDistance,facesAngle'getters 
    ...mapGetters(['getBlastVal'])
  },
  watch:{
    blastVal:{
        handler(newVal, oldVal) {
            console.log(newVal, oldVal,'in watch')
          //  this.blastChange(newVal) 
        },
        deep: true,
        immediate: true

      }


  },  
  methods:{
    // 
    async nativePick (e){
     const val =  e ? e.target.value : null
      if (val) {
        console.log(': ' + val)
      } else {
        const eyeDropper = new window.EyeDropper() // EyeDropper
        console.log('Esc')
        try {
          const result = await eyeDropper.open() // 
          console.log(': ' + result.sRGBHex)
        } catch (e) {
          console.log('')
        }
      }
    },
    switchleft(){
      this.isActive = !this.isActive
    },  
    // 
    changeWange(val){
       this.wanggeValue = val
      //  console.log(this.website,'this.website')
      // //  this.$nextTick(() => {
        //  this.model = this.website.model
         this.website.wireFrame.setWireFrame(this.wanggeValue,this.website.model)
      // //  })  
     
    },
    // 
    backgroundTabClick(index){
       this.tabIndex = index 

       this.website.viewer.outline.SetbackgroundColor(index)
    },
    //
    SetEnvironment(envMapName, backgroundIsEnvMap){
      this.website.viewer.outline.SetEnvironment(envMapName, backgroundIsEnvMap)
    },  
    // 
    blastChange(val){
       console.log(this,'this in blastChange')
      //  this.website.Blast(this.website.model,val)
      //  js
      // this.website.addEventListener('start',function(event){
      //    alert(event.message)
      // })
      // this.website.start(val)
      // js vue
          this.website.viewer.outline.Blast(this.website.model,val)
        //   this.website.viewer.outline.addEventListener('start',function(event){
        //     alert(event.message)
        //  })
        //  this.website.viewer.outline.start()
      
    },
    //edge 
    edgeChange(val){
      this.website.viewer.outline.Edge(this.website.model,val)
    },
    // 
    autoRotation(val){
      this.rotationValue = val
      this.website.viewer.outline.rotation(this.rotationValue)
    },
    duxian(){
      // this.duxianBtn = !this.duxianBtn
      // this.website.viewer.outline.duxian(this.website.model,this.duxianBtn)
      // this.duxianBtn =ifduxian err
       this.website.viewer.outline.duxian(this.website.model,!this.$store.state.app.duxian)
    },
    dragMesh(){
      this.dragMeshBtn =    !this.dragMeshBtn
      if(this.dragMeshBtn ){
        // this.website.wireFrame.dragMesh(this.website.model)
       
      }
     
      this.website.viewer.outline.dragMesh(this.website.model,this.dragMeshBtn)
     
    },
    resetDragMesh(){
     
      this.dragMeshBtn = false
      this.website.wireFrame.resetDragMesh(this.website.model)
    
    },
    dragMeshs(){
       this.dragMeshBtn = !this.dragMeshBtn
       this.website.wireFrame.dragMeshs(this.dragMeshBtn,this.website.model)
    },
    reset(){
      this.website.viewer.outline.reset(this.website.model)
    },
 
    touming(){
      this.toumingBtn = !this.toumingBtn
      this.website.viewer.outline.touming(this.website.model,this.toumingBtn)
    },
    touming2(){
      this.website.viewer.outline.touming(this.website.model,true)
    },
    fullScreen(el){
      // console.log( isFullscreenEnabled(),' isFullscreenEnabled()')
      // console.log(getFullscreenElement(),'getFullscreenElement')
      // console.log(isFullScreen(),'isFullScreen')
      this.fullScreenBtn=!this.fullScreenBtn
      // way1
      // let elem = document.getElementById('model-wrapper')
      // if( this.fullScreenBtn){
      //   openFullscreen(elem)
      // }else {
      //   exitFullscreen(elem)
      // }
     
      // way2
      el.preventDefault()
      let elem = document.getElementById('model-wrapper')
      // let elem = document.getElementById('main_viewer')
      // let elem = document.documentElement
    
      fullScreens(elem)
    },
    yincang(){
      this.website.viewer.outline.yincang(this.website.model,true)
    },
    // 
    celiang(){
      this.website.Measure(!this.$store.state.MeasureisActive)
    },
    // iframe 
    handleMessage(event){
      console.log(event,'event in handleMessage')
      const data = event
    }
  },  
  mounted () {
    console.log(this.$store,'this.$store')
  SetWebsiteEventHandler((eventName, eventLabel, eventParams) => {
        console.log({
          eventName,
          eventLabel,
          eventParams
        })
 })
     this.website = StartWebsite('./libs',this.$store.state.app.edgeVal)
     window.addEventListener('message',this.handleMessage)
}
}
</script>
<style lang="scss" scoped>
.viewer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;

  .model-wrapper {
    border: 1px solid green;
    position: relative;
    /* width: 700px;
      height: 700px; */
    width: 100%;
    height: 100%;

    .main_viewer {
      // border: 1px solid blue;
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      // width: 100%;
      // height: 100vh;

    }

    .side_menu_wrap {
      position: absolute;
      top: 0;
      bottom: 0;
      padding: 20px 10px;
      width: 20%;
      background-color: rgba(0, 0, 0, .5);
      color: #fff;
      transition: all .4s;

      /* z-index: 9999; */
      /* transition: all .4s cubic-bezier(.7, .3, .1, 5); */
      .side-menu {
        /* border: 1px solid blue; */
        width: 100%;
        height: 100%;
        // overflow-y: scroll;
        overflow-x: hidden;

        .label_row {
          /* margin: 20rem 0 10rem 0; */
          margin: 20px 0 10px 0;
          display: flex;
          justify-content: space-between;
          font-size: 16px;
          user-select: none;
        }

        .blast-slider {
          padding: 0 10px;
        }

        // tab 
        .btns {
          border-radius: 4rem;
          overflow: hidden;
          user-select: none;

          span {
            display: inline-block;
            width: 33.3%;
            // height: 24rem;
            height: 24px;
            background-color: #fff;
            color: #182c49;
            text-align: center;
            // line-height: 24rem;
            line-height: 24px;
            cursor: pointer;
            // font-size: 12rem;
            font-size: 12px;
          }

          .active {
            background-color: #1458ea;
            color: #fff;
          }
        }



        .row {
          width: 100%;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          /* justify-content: space-around; */
          /* align-items: center; */
          margin-bottom: 20px;

          span {
            user-select: none;
          }

        }

        // 
        .envMaps {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          justify-content: flex-start;
          align-items: center;
          padding: 0 10px;

          .img {
            // TODO
            margin-right: 5px;
            margin-bottom: 5px;
          }

        }
      }

      .switch {
        position: absolute;
        top: 50vh;
        right: -20px;
        z-index: 999;
        transform: translateY(-40px);
        width: 20px;
        height: 80px;
        background-color: rgba(0, 0, 0, .5);
        text-align: center;
        line-height: 80px;
        border-radius: 0 4px 4px 0;
        cursor: pointer;
        transition: all .4s;
      }
    }
  }
}

.measureInfo {
  position: absolute;
  top: 20px;
  left: 45%;
  color: #fff;

  // span:nth-child(1) {
  //   color: red;
  //   margin-right: 20px;
  // }


  span {

    i,
    b {
      margin-right: 10px;
    }
  }
}

.full-sceen {
  position: absolute;
  bottom: 12px;
  right: 20px;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  color: #fff;
  border-radius: 100%;
  cursor: pointer;
}






.side_menu_wrap .items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  b {
    user-select: none;
  }
}

.side_menu_wrap .items span {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  /* padding: 10rem 0; */
  padding: 10px 0;
}

.side_menu_wrap .items span :nth-child(2) {
  margin: 0 16px;
}

.side_menu_wrap .items span b {
  display: block;
  /* width: 36rem; */
  width: 36px;
  text-align: center;
  margin-top: 5px;
  font-size: 14px;
}

.side_menu_wrap .items span .iconfont {
  /* width: 36rem;
height: 36rem; */
  width: 36px;
  height: 36px;
  color: #fff;
  font-size: 18px;
  line-height: 36px;
  text-align: center;
  border-radius: 18px;
  background-color: rgba(0, 0, 0, .2);
  /*  */
  margin-bottom: 5px;

}

.side_menu_wrap .items .active .iconfont {
  background-color: #1458ea;
}

.left0 {
  transform: translate(-100%);
}

.left200 {
  transform: translate(0);
}

// 
div.ov_measure_panel {
  padding: 6px 15px;
  position: absolute;
  border-radius: 30px;
  left: 100px;
  top: 0px;
}

div.ov_measure_panel div.ov_svg_icon {
  color: inherit;
  margin-bottom: 2px;
}

div.ov_measure_panel div.ov_measure_value {
  float: left;
  margin-right: 10px;
}
</style>