// URL.revokeObjectURL()  URL.createObjectURL()  URL  URL 
import * as THREE from 'three'
import { TGALoader } from 'three/addons/loaders/TGALoader.js'
import { LoaderUtils } from './LoaderUtils.js'
import { RevokeObjectUrl } from '../engine/io/bufferutils.js'

function addLoadModeltoScene (object, callbacks) {
  // editor.execute(new AddObjectCommand(editor, object)))
  // (1) scene  this 
  // this.viewer.scene.add(object)
  // 2 callbacks  websitecallback
  // callbacks.render(object)
  callbacks.onModelFinished(object)
  // (3)
  // (4)  vuex
  // (5) sinals 
}
var that;
export class DragLoader {
  constructor (viewer) {
    that = this;
    this.viewer = viewer
    this.callbacks = null
    // URL
    this.objectURLs = []
  }

  // file blob
  RevokeObjectUrls () {
    console.log(this.objectURLs, 'this.objectURLs in RevokeObjectUrls')
    if (this.objectURLs === null) {
      return
    }
    for (let objectUrl of this.objectURLs) {
      RevokeObjectUrl(objectUrl)
    }
    this.objectURLs = []
  }

  DragLoadFiles (files, filesMap, callbacks) {
    this.callbacks = callbacks
    if (files.length > 0) {
      console.log(files, 'files in DragLoadFiles')
      filesMap = filesMap || LoaderUtils.createFilesMap(files)
      console.log(filesMap, 'filesMap in DragLoadFiles')
      // TODO
      // https://blog.csdn.net/damadashen/article/details/125695071
      // 
      const manager = new THREE.LoadingManager()
      // //        
      // // 
      // manager.onStart = function (url, itemsLoaded, itemsTotal) {
      //   console.log('-Started loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.')
      // }
      // // 
      // manager.onLoad = function () {
      //   console.log('-Loading complete!')
      // }
      // // 
      // manager.onProgress = function (url, itemsLoaded, itemsTotal) {
      //   console.log('-Loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.')
      // }
      // // 
      // manager.onError = function (url) {
      //   console.log('-There was an error loading ' + url)
      // }

      // URL
      // manager.setURLModifier((url) => {

      // })
      // blob url
      manager.setURLModifier((url) => {
        console.log(url, 'url in setURLModifier')
        url = url.replace(/^(\.?\/)/, '') // remove './'
        const file = filesMap[url]
        console.log('file-url', file)
        if (file) {
          console.log('Loading in setURLModifier', url)
          console.log(URL.createObjectURL(file), 'URL.createObjectURL(file)')
          this.objectURLs.push(URL.createObjectURL(file))
          return URL.createObjectURL(file)
        } else {
          // alert(`${url}`)
        }

        return url
      })
      console.log(this.objectURLs, 'objectURLs')
      // tga 
      manager.addHandler(/\.tga$/i, new TGALoader())
      for (let i = 0; i < files.length; i++) {
        this.DragLoadFile(files[i], manager, callbacks)
      }
    }
  }

  // 
  DragLoadFile (file, manager, callbacks) {
    console.log(this, 'this in DragLoadFile')
    const filename = file.name
    const extension = filename.split('.').pop().toLowerCase()
    const reader = new FileReader()
    reader.addEventListener('progress', function (event) {
      const size = '(' + Math.floor(event.total / 1000).format() + ' KB)'
      const progress = Math.floor((event.loaded / event.total) * 100) + '%'

      console.log('Loading', filename, size, progress)
    })
    switch (extension) {
      case 'fbx':

      {
        reader.addEventListener('load', async function (event) {
          const contents = event.target.result

          const { FBXLoader } = await import('three/addons/loaders/FBXLoader.js')

          const loader = new FBXLoader(manager)
          const object = loader.parse(contents)

          console.log(this.objectURLs, 'this.objectURLs in fbx')
          // this.objectURLs.forEach((url) => {
          //   // TODO  URL.revokeObjectURL(url)
          //   // blob
          //   console.log(url, URL.revokeObjectURL(url), 'URL.revokeObjectURL(url)')
          //   return URL.revokeObjectURL(url)
          // })
          //  that.viewer.scene.add(object)  //ok
          // this.viewer.scene.add.call(that.viewer.scene,object) err
          // console.log(this, 'this in dragloader') // fileReader
          addLoadModeltoScene(object, callbacks)
        }, false)
        reader.readAsArrayBuffer(file)

        break
      }
      case 'obj':

      {
        reader.addEventListener('load', async function (event) {
          const contents = event.target.result

          const { OBJLoader } = await import('three/addons/loaders/OBJLoader.js')

          const object = new OBJLoader().parse(contents)
          object.name = filename

          // editor.execute(new AddObjectCommand(editor, object))

          addLoadModeltoScene(object, callbacks)
        }, false)
        reader.readAsText(file)

        break
      }
    }
  }
}
