const state = {
  showLoading: false, // loading
  blastVal:1, //TODO  10
  duxian:false,
  edgeVal:100
}
const mutations = {
  // 
  DUXIAN(state,val){
     state.duxian= val
  },
  // loading
  SHOW_LOADING (state, val) {
    state.showLoading = false
  },
  // 
  CHANGE_BLASTVAL(state, val){
     state.blastVal = val
  },
  RESET_BLASTVAL(state){
    state.blastVal = 1
  },
  // edge

  CHANGE_EDGE_VAL(state,val){
     state.edgeVal = val
  }
}
const actions = {
  showLoading ({ commit }, val) {
    commit('SHOW_LOADING', false)
  },
 changBlastVal({ commit }, val){
  commit('CHANGE_BLASTVAL', val)
 },
 resetBlastVal({ commit }, val){
  commit('RESET_BLASTVAL', val)
 }
}
export default {
  namespaced: true,
  state,
  mutations,
  actions
}
