    if (boundingSphere === null) {
      return
    }
    // let center = [boundingSphere.center.x, boundingSphere.center.y, boundingSphere.center.z]
    let center = new Coord3D(boundingSphere.center.x, boundingSphere.center.y, boundingSphere.center.z)
    console.log(center, 'center in  FitSphereToWindow')
    let radius = boundingSphere.radius
    // let newCamera = this.GetFitToSphereCamera(center, radius)
    let newCamera = this.navigation.GetFitToSphereCamera(center, radius)
    console.log(newCamera, 'newCamera')
     // self tween 
    // this.outline.tweenCamera(newCamera,1000)
    this.navigation.MoveCamera(newCamera, animation ? this.settings.animationSteps : 60)
   
