// loader
import { ThreeModelLoader } from '../engine/threejs/threemodelloader.js'
export class ThreeModelLoaderUI {
  constructor () {
    this.modelLoader = new ThreeModelLoader()
  }

  LoadModel (inputFiles, settings, callbacks) {
    console.log(inputFiles, 'inputFiles in ThreeModelLoaderUI')
    console.log(settings, 'settings')
    console.log(callbacks, 'callbacks')
    if (this.modelLoader.InProgress()) {
      return
      //  
    }

    this.modelLoader.LoadModel(inputFiles, settings, {
      onLoadStart: () => {
        callbacks.onStart()
      },
      // list
      onFileListProgress: (current, total) => {
        console.log(current, total, 'onFileListProgress')
      },
      // getcontent 
      onFileLoadProgress: (current, total) => {
        console.log(current, total, 'onFileLoadProgress')//  
      },
      onSelectMainFile: () => {},
      onImportStart: () => {},
      onVisualizationStart: () => {},
      onModelFinished: () => {},
      onTextureLoaded: () => {},
      onLoadError: () => {}
    })
  }
}
