import * as THREE from 'three'
import { DragControls } from 'three/addons/controls/DragControls.js'
export class WireFrame {
   constructor(website){

      this.viewer = website.viewer
      this.model = null;
      this.cloneModel = null;
      this.dragControls = null
      // this.camera = website.viewer.camera
      // console.log(website.viewer.renderer,'this.cameraWireFrame') //null this.viewer.init loaderconstruct
      // this.renderer = website.viewer.renderer\
      this.status = ''
  
   }
   duxian(){
     
   }
  //  
   setWireFrame(val,model){
    console.log(val,'val in setWireFrame ')
    // this.model = model
    model.traverse((child)=>{
      if (child instanceof THREE.Mesh) {
           if(val){
            if (child.material.length > 1) {
              for (var i = 0; i < child.material.length; i++) {
                child.material[i].wireframe = true
              }
            } else {
              child.material.wireframe = true
            }
           }else {
            if (child.material.length > 1) {
              for (var i = 0; i < child.material.length; i++) {
                child.material[i].wireframe = false
              }
            } else {
              child.material.wireframe = false
            }
           }
      }
    })
   }
  //  
  dragMesh(model){
    
    if(!this.dragControls){
      this.model = model
      this.cloneModel = model.clone()
      this.dragControls = new DragControls([this.model ], this.viewer.camera, this.viewer.renderer.domElement)
      this.dragControls.activate();
         
    this.dragControls.addEventListener('dragstart', this.dragstart.bind(this))
    // 
    this.dragControls.addEventListener('dragend', this.dragend.bind(this))
    }else {
     
      this.dragControls.removeEventListener('dragstart',this.dragstart.bind(this))
      this.dragControls.removeEventListener('dragend',this.dragend.bind(this))
      this.dragControls.deactivate();
      this.dragControls = null
     this.viewer.scene.remove(this.dragControls)
    
      this.dragControls = new DragControls([this.cloneModel ], this.viewer.camera, this.viewer.renderer.domElement)
      this.dragControls.addEventListener('dragstart', this.dragstart.bind(this))
    // 
    this.dragControls.addEventListener('dragend', this.dragend.bind(this))
     
    }
  


  }
//    dragControlsStart() {
//     this.dragControls.addEventListener('dragstart', function (event) {
//       this.viewer.controls.enabled = false
//     });
//     this.dragControls.addEventListener('dragend', function (event) {
//       this.viewer.controls.enabled = true
//     });
// }
// dragControlsStop() {
//   this.dragControls.removeEventListener('dragstart', function (event) {
//     this.viewer.controls.enabled = false
//   });
//   thsi.dragControls.removeEventListener('dragend', function (event) {
//     this.viewer.controls.enabled = true
//   });
// }
dragstart(){
  this.viewer.controls.enabled = false
}
dragend(){
  this.viewer.controls.enabled = true
}
  // mesh
  resetDragMesh(){
      this.status = ''
      this.viewer.scene.remove(this.model)
      this.viewer.scene.add(this.cloneModel)
      this.model = this.cloneModel 
      // this.dragControls.dispose();

      
    
  }
  dragMeshs(val,model){
    //  console.log(this.viewer.model,'23423423432')
       if(val){
           this.dragMesh(model)
           this.dragControls.addEventListener('dragstart', this.dragstart.bind(this))
           // 
           this.dragControls.addEventListener('dragend', this.dragend.bind(this))
       }else {
        this.dragControls.removeEventListener('dragstart',this.dragstart.bind(this))
        this.dragControls.removeEventListener('dragend',this.dragend.bind(this))
        this.viewer.scene.remove(this.model)
        this.viewer.scene.add(this.cloneModel)
        this.dragMesh(this.cloneModel)
       }

  }
}